*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.state{
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 0 auto;
  border: 2px solid #3f2a02;
  border-radius: 10px;
  padding: 30px;
  background-color: rgb(251, 243, 233);
  position: relative;
}

.state h1{
  text-align: center;
  color: #583a03;
  margin-bottom: 20px;
}

.state img{
  width: 570px;
  height: 500px;
  object-fit: cover;

  display: flex;
  margin: 0 auto;
  margin-bottom: 30px;
  border: 2px solid #8c7f67;
  border-radius: 10px;

}


.state input{
  display: block;
  width: 100%;
  height: 50px;
  background-color: rgb(202, 186, 166);
  border: 2px solid #564005;
  border-radius: 10px;
  margin-bottom: 10px;

}


.state input .type .name{
  color: tomato;
}

.state button{
  width: 100px;
  height: 50px;
  background-color: rgb(80, 56, 2);
  border-radius: 5px;
  color: aliceblue;
  position: absolute;
  right: 30px;
  bottom: 57px;
  
}

.state p{
  color: rgb(116, 78, 19);
  font-size: 13px;
  padding-right: 50px;

}

.state p span{
  background-color: tomato;
}











